export const ACTIVATE_TIMER = "ACTIVATE_TIMER";
export const RESET_TIMER = "RESET_TIMER";
export const UPDATE_TITLE = "UPDATE_TITLE";
export const UPDATE_MINUTE = "UPDATE_MINUTE";
export const UPDATE_TERM = "UPDATE_TERM";
export const ADD_PROGRESS = "ADD_PROGRESS";

export function activateTimer(b) {
  return { type: ACTIVATE_TIMER, payload: b };
}
export function resetTimer() {
  return { type: RESET_TIMER, payload: "" };
}

export function updateTitle(title) {
  return { type: UPDATE_TITLE, payload: title };
}

export function updateMinute(minute) {
  return { type: UPDATE_MINUTE, payload: minute };
}
export function updateTerm(term) {
  return { type: UPDATE_TERM, payload: term };
}
export function addProgress(progress) {
  return { type: ADD_PROGRESS, payload: progress };
}
