import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addProgress } from "./actions";
import style from "./Timer.scss";
import UIfx from "uifx";
import endMelody from "./sound/EndMelody.mp3";

const Timer = () => {
  const dispatch = useDispatch();
  let { activeTimer, minute, progress } = useSelector(state => state);
  minute = minute ? minute : 30;
  useEffect(() => {
    if (activeTimer && progress < 1) {
      setTimeout(() => {
        dispatch(addProgress(Math.min(progress + 1 / minute / 60, 1)));
      }, 1000);
    }
  });
  const strokeDashoffset = -style.mainCir * progress;
  const finishMessage = () => {
    if (progress == 1) {
      const tick = new UIfx(endMelody);
      tick.play();
      return (
        <div className="finishMessage">
          <div>Done!</div>
        </div>
      );
    }
  };
  return (
    <div className="timer__container">
      {finishMessage()}
      <div hidden={progress == 1}>
        <svg className="timer" viewBox="0 0 100 100">
          <circle
            className="timer__common timer__main"
            style={{ strokeDashoffset }}
          />
          <circle
            className="timer__common timer__scale timer__scale--1min"
            strokeDasharray={`${0.5} ${style.subCir / minute - 0.5}`}
          />
          <circle
            className="timer__common timer__scale timer__scale--10min"
            strokeDasharray={`${1} ${(style.subCir / minute) * 10 - 1}`}
          />
        </svg>
      </div>
    </div>
  );
};

export default Timer;
