import React from "react";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import "./App.scss";
import {
  activateTimer,
  resetTimer,
  updateTitle,
  updateMinute,
  updateTerm
} from "./actions";
import Timer from "./Timer";

const App = () => {
  const dispatch = useDispatch();
  const { activeTimer, term, title, minute, progress } = useSelector(
    state => state
  );
  return (
    <div className="App">
      <div className="container">
        <Timer />
        <input
          className="input input__title"
          type="text"
          value={title}
          placeholder="What do you focus?"
          onChange={e => dispatch(updateTitle(e.target.value))}
          hidden={activeTimer}
        ></input>
        <input
          className="input input__minute"
          type="number"
          value={minute}
          placeholder="Minutes?"
          onChange={e => dispatch(updateMinute(e.target.value))}
          hidden={activeTimer}
        ></input>
        <button
          className="button__start"
          onClick={() => {
            dispatch(activateTimer(true));
            dispatch(
              updateTerm(
                moment().format("HH:mm") +
                  " ~ " +
                  moment()
                    .add(minute ? minute : 30, "minutes")
                    .format("HH:mm")
              )
            );
          }}
          hidden={activeTimer}
        >
          Start
        </button>
        <div className="title_container">
          <div
            className="title"
            hidden={!activeTimer}
            style={{
              animation: `${progress != 1 ? "blink 3s linear infinite" : ""}`
            }}
          >
            {title}
          </div>
        </div>
        <div className="term" hidden={!activeTimer}>
          {term}
        </div>
        <button
          className="button__start"
          hidden={progress != 1}
          onClick={() => {
            dispatch(resetTimer());
          }}
        >
          NEW TIMER
        </button>
      </div>
    </div>
  );
};

export default App;
