import { combineReducers } from "redux";
import {
  ACTIVATE_TIMER,
  RESET_TIMER,
  UPDATE_TITLE,
  UPDATE_MINUTE,
  UPDATE_TERM,
  ADD_PROGRESS
} from "../actions";

const activeTimer = (state = false, { type, payload }) => {
  switch (type) {
    case ACTIVATE_TIMER:
      return payload;
    case RESET_TIMER:
      return false;
    default:
      return state;
  }
};

const title = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_TITLE:
      return payload;
    case RESET_TIMER:
      return "";
    default:
      return state;
  }
};
const minute = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_MINUTE:
      return payload;
    default:
      return state;
  }
};
const term = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_TERM:
      return payload;
    default:
      return state;
  }
};
const progress = (state = "", { type, payload }) => {
  switch (type) {
    case ADD_PROGRESS:
      return payload;
    case RESET_TIMER:
      return 0.0;
    default:
      return 0.0;
  }
};
export default combineReducers({
  activeTimer,
  term,
  title,
  minute,
  progress
});
